.my-gallery {
    width: 100%;
    float: left;
}

.my-gallery img {
    display: block;
    width: 100%;
    height: auto;
}

.my-gallery figure {
    display: block;
    float: left;
    margin: 0 5px 5px 0;
    width: 100%;
}

.my-gallery figcaption {
    display: none;
}

.my-gallery figure:not(:first-child) {
    display: none;
}
